import photo from "./assets/photo.jpeg";
import "./assets/about.css";

import React from "react";
import PropTypes from "prop-types";

class About extends React.Component {
  static contextTypes = {
    mixpanel: PropTypes.object.isRequired
  };

  render() {
    return (
      <div className="row">
        <div className="col s12 m2 l2 center">
          <img
            src={photo}
            className="circle personal-img pointer-cursor tooltipped"
            alt="future-easter-egg"
            data-position="left"
            data-delay="50"
            data-tooltip="This is the v1.0.0!"
          />
        </div>
        <div className="col s12 m10 l10">
          <h4 className="light about-header">Hi there, I'm Rafael Nunes.</h4>
          <p className="about-text">
            Looking forward to collaborate with projects that generates impact!
          </p>
          {this.renderContacts()}
        </div>
      </div>
    );
  }

  renderContacts() {
    return (
      <div className="row">
        {CONTACTS.map((contactMethod, index) => {
          return (
            <div key={`${contactMethod.title}_${index}`}>
              <div className="col m1 l1 hide-on-small-only show-on-medium-and-up">
                {this.renderContactMethod(contactMethod)}
              </div>
              <div className="col s3 hide-on-med-and-up">
                {this.renderContactMethod(contactMethod)}
              </div>
            </div>
          );
        })}
      </div>
    );
  }

  renderContactMethod(contactMethod) {
    return (
      <a
        title={contactMethod.title}
        href={contactMethod.href}
        className="contact-a"
        target="_blank"
        rel="noopener noreferrer"
        onClick={e => this.trackClick(e, contactMethod.href)}
      >
        <i
          name={contactMethod.href}
          className={`${contactMethod.icon} contact-icon`}
          aria-hidden="true"
        />
      </a>
    );
  }

  trackClick(event, href) {
    this.context.mixpanel.track("contact_link_clicked", {
      link: href
    });
  }
}

const CONTACTS = [
  {
    title: "My posts on Medium!",
    href: "https://medium.com/@peaonunes",
    icon: "fa fa-medium"
  },
  {
    title: "Some code on Github!",
    href: "https://github.com/peaonunes",
    icon: "fa fa-github"
  },
  {
    title: "My resume on Linkedin!",
    href: "https://www.linkedin.com/in/rngds",
    icon: "fa fa-linkedin-square"
  },
  {
    title: "Email me!",
    href: "mailto:rafaelngds@gmail.com",
    icon: "fa fa-envelope"
  }
];

export default About;



